.app-container {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 2rem;
  align-items: center;
  margin-top: 80px;
}

table {
  border-collapse: collapse;
  width: 60%;
}

th,
td {
  border: 1px solid #ffffff;
  text-align: left;
  padding: 8px;
  font-size: 20px;
}

th {
  background-color: rgb(117, 201, 250);
}

td {
  background-color: rgb(205, 235, 253);
}
